"use client";

import { useEffect, useState } from "react";
import { useAuth } from "@/context/AuthContext";
import { TrendingUp, FileText, Users } from "lucide-react";

export default function AdminDashboard() {
    const { token } = useAuth();
    const [stats, setStats] = useState({
        stocks: 0,
        blogs: 0,
        users: 0
    });

    useEffect(() => {
        // In a real app, we would fetch these stats from the backend
        // For now, we'll just mock them or fetch what we can
        const fetchStats = async () => {
            try {
                const stocksRes = await fetch("http://localhost:8000/api/v1/watchlist");
                const stocksData = await stocksRes.json();

                const blogsRes = await fetch("http://localhost:8000/api/v1/blogs");
                const blogsData = await blogsRes.json();

                setStats({
                    stocks: Array.isArray(stocksData) ? stocksData.length : 0,
                    blogs: Array.isArray(blogsData) ? blogsData.length : 0,
                    users: 1 // Just the current user for now
                });
            } catch (error) {
                console.error("Failed to fetch stats", error);
            }
        };

        if (token) {
            fetchStats();
        }
    }, [token]);

    return (
        <div className="space-y-8">
            <h2 className="text-3xl font-bold text-white">Dashboard Overview</h2>

            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                <div className="bg-gray-800 p-6 rounded-2xl border border-gray-700">
                    <div className="flex items-center justify-between mb-4">
                        <h3 className="text-gray-400 font-medium">Tracked Stocks</h3>
                        <div className="p-2 bg-blue-500/10 rounded-lg">
                            <TrendingUp className="w-6 h-6 text-blue-400" />
                        </div>
                    </div>
                    <p className="text-3xl font-bold text-white">{stats.stocks}</p>
                </div>

                <div className="bg-gray-800 p-6 rounded-2xl border border-gray-700">
                    <div className="flex items-center justify-between mb-4">
                        <h3 className="text-gray-400 font-medium">Published Blogs</h3>
                        <div className="p-2 bg-purple-500/10 rounded-lg">
                            <FileText className="w-6 h-6 text-purple-400" />
                        </div>
                    </div>
                    <p className="text-3xl font-bold text-white">{stats.blogs}</p>
                </div>

                <div className="bg-gray-800 p-6 rounded-2xl border border-gray-700">
                    <div className="flex items-center justify-between mb-4">
                        <h3 className="text-gray-400 font-medium">Total Users</h3>
                        <div className="p-2 bg-green-500/10 rounded-lg">
                            <Users className="w-6 h-6 text-green-400" />
                        </div>
                    </div>
                    <p className="text-3xl font-bold text-white">{stats.users}</p>
                </div>
            </div>
        </div>
    );
}
